.container {
  min-height: 100vh;
  /* background: #102626; */

  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container .loader {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-color: #2b2b2b;
  animation: animate 3s linear infinite;
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.container .loader::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background: linear-gradient(to top, transparent, rgba(176, 176, 176, 0.4));
  background-size: 100px 180px;
  background-repeat: no-repeat;
  border-top-left-radius: 100px;
  border-bottom-left-radius: 100px;
}

.container .loader::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 20px;
  height: 20px;
  background: #5e5e5e;
  border-radius: 50%;
  z-index: 10;
  box-shadow: 0 0 10px #5e5e5e, 0 0 20px #5e5e5e, 0 0 30px #5e5e5e,
    0 0 40px #5e5e5e, 0 0 50px #5e5e5e, 0 0 60px #5e5e5e, 0 0 70px #5e5e5e,
    0 0 80px #5e5e5e, 0 0 90px #5e5e5e, 0 0 100px #5e5e5e;
}

.container .loader span {
  position: absolute;
  top: 20px;
  left: 20px;
  bottom: 20px;
  right: 20px;
  background: #181818;
  border-radius: 50%;
}
